body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  color: #00b6ff;
  text-decoration: none; }

button {
  background-color: #406a7b;
  border-radius: 4px;
  color: white;
  padding: 12px 32px;
  font-size: 1.6em; }
  button:hover {
    background-color: #477688; }

.wrap {
  padding: 0 24px;
  max-width: 1280px;
  margin: 0 auto;
  color: #406a7b; }

header {
  overflow: auto; }
  header h1 {
    float: left; }
  header nav {
    float: right;
    font-size: 1.6em;
    margin-top: 27px; }

main textarea {
  width: calc(100% - 48px);
  background: #3a3a3a;
  padding: 24px;
  border-radius: 4px;
  margin-bottom: 24px;
  outline: none;
  resize: vertical;
  border: 0 black;
  color: white;
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
  font-size: 1em;
  line-height: 1.6em; }
  main textarea:focus {
    box-shadow: 0 0 3px 2px #00b6ff; }

main label {
  margin-right: 24px;
  padding-left: 4px; }

main button {
  float: right;
  border-radius: 100px;
  padding: 11px 32px 14px; }
  main button:before {
    content: "\25B6";
    padding-right: 12px;
    top: 1px;
    position: relative; }

main .wrap-input {
  overflow: auto; }

main .wrap-radio {
  margin-top: 20px;
  float: left; }

main .wrap-select {
  float: left;
  margin-top: 20px;
  margin-left: 80px; }

main div.result {
  margin-bottom: 128px; }
  main div.result p.output {
    font-style: italic;
    color: #989898;
    line-height: 1.4em; }
  main div.result code {
    white-space: pre-wrap; }

